import React, { useState, useEffect } from "react";
import "bootstrap/dist/css/bootstrap.min.css";
import logo from "../images/logo.png";
import {
  Collapse,
  Navbar,
  NavbarToggler,
  NavbarBrand,
  Nav,
  NavItem,
  NavLink,
  Alert,
} from "reactstrap";
import { Link } from "react-router-dom";
import SignUpForm from "../components/singupForm";
import Login from "../components/loginForm";
import Logout from "../components/logoutForm";
import { useAuth0 } from "@auth0/auth0-react";

function MyNavbar() {
  const [isOpen, setIsOpen] = useState(false);
  const [showRecommendations, setShowRecommendations] = useState(false);
  const [alertVisible, setAlertVisible] = useState(false);
  const { isAuthenticated } = useAuth0();

  const toggle = () => setIsOpen(!isOpen);

  useEffect(() => {
    const checkHeartbeat = async () => {
      try {
        const response = await fetch(
          `${process.env.REACT_APP_URL_API}/heartbeat`
        );
        const data = await response.json();

        if (data.status === "operational") {
          setShowRecommendations(true);
        } else {
          throw new Error("Service not operational");
        }
      } catch (error) {
        setShowRecommendations(false);
        setAlertVisible(true);
      }
    };
    checkHeartbeat();
  }, []);

  return (
    <div>
      {alertVisible && (
        <Alert color="danger" toggle={() => setAlertVisible(false)}>
          El servicio no está operativo. No se pueden cargar las
          recomendaciones.
        </Alert>
      )}
      <Navbar color="light" fixed="top" expand="sm">
        <NavbarBrand tag={Link} to="/">
          <img
            alt="logo"
            src={logo}
            style={{
              height: 30,
              width: 30,
              marginRight: "10px",
            }}
          />
          G8 Gambling
        </NavbarBrand>
        <NavbarToggler onClick={toggle} aria-label="Toggle navigation" />
        <Collapse isOpen={isOpen} navbar>
          <Nav className="me-auto" navbar>
            <NavItem>
              <NavLink tag={Link} to="/matches">
                Partidos
              </NavLink>
            </NavItem>
          </Nav>
          <Nav className="ms-auto" navbar>
            {isAuthenticated ? (
              <>
                <NavItem>
                  <NavLink href="/mybonds">Mis Bonos</NavLink>
                </NavItem>
                {showRecommendations && (
                  <NavItem>
                    <NavLink href="/myrecomendations">Recomendaciones</NavLink>
                  </NavItem>
                )}
                <NavItem>
                  <NavLink href="/mywallet">Billetera</NavLink>
                </NavItem>
                <NavItem>
                  <Logout />
                </NavItem>
              </>
            ) : (
              <>
                <NavItem>
                  <Login />
                </NavItem>
                <NavItem>
                  <SignUpForm />
                </NavItem>
              </>
            )}
          </Nav>
        </Collapse>
      </Navbar>
    </div>
  );
}

export default MyNavbar;
